import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import $ from 'jquery';

class App extends Component {
  render() {
    return (
      <div className="App">

        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1>Welcome to Represent</h1>
        </header>

        <div className="container">

          <div className="row">
            <div className="six columns">
              <h4>What is Lorem Ipsum</h4>
              <p>Lorem Ipsum is simply dummy text of the printing and typesetting 
              industry. Lorem Ipsum has been the industry's standard dummy text 
              ever since the 1500s, when an unknown printer took a galley of type 
              and scrambled it to make a type specimen book. It has survived not 
              only five centuries, but also the leap into electronic typesetting, 
              remaining essentially unchanged. It was popularised in the 1960s with 
              the release of Letraset sheets containing Lorem Ipsum passages, and 
              more recently with desktop publishing software like Aldus PageMaker 
              including versions of Lorem Ipsum.</p>
            </div>
            <div className="six columns">
              <h4>What is Lorem Ipsum</h4>
              <p>Lorem Ipsum is simply dummy text of the printing and typesetting 
              industry. Lorem Ipsum has been the industry's standard dummy text 
              ever since the 1500s, when an unknown printer took a galley of type 
              and scrambled it to make a type specimen book. It has survived not 
              only five centuries, but also the leap into electronic typesetting, 
              remaining essentially unchanged. It was popularised in the 1960s with 
              the release of Letraset sheets containing Lorem Ipsum passages, and 
              more recently with desktop publishing software like Aldus PageMaker 
              including versions of Lorem Ipsum.</p>
            </div>
          </div>

          <div className="row">
            <div className="one-third column">
              <img src={logo} className="App-logo-lg" alt="logo" />
            </div>
            <div className="one-third column">
              <img src={logo} className="App-logo-lg" alt="logo" />
            </div>
            <div className="one-third column">
              <img src={logo} className="App-logo-lg" alt="logo" />
            </div>
          </div>
          <div className="row">
            <div className="twelve columns">
              <ControlledForm />
            </div>
          </div>
        </div>

        <footer className="App-footer">
          <div className="container">
            <div className="row">
              <div className="twelve columns">
                <p>The standard chunk of Lorem Ipsum used since the 1500s is reproduced 
                below for those interested. Sections 1.10.32 and 1.10.33 from "de Finibus 
                Bonorum et Malorum" by Cicero are also reproduced in their exact original 
                form, accompanied by English versions from the 1914 translation by H. Rackham.</p>
              </div>
            </div>
          </div>
        </footer>
      </div>
    );
  }
}

class ControlledForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      phone_number: '',
      choice_input: '',
      message: ''
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({[event.target.name]: event.target.value});
  }

  resetForm() {
    this.setState({
      phone_number: '',
      choice_input: '',
      message: ''
    });
  }

  handleSubmit(event) {

    $.ajax({
      method: "POST",
      url: "/users/sms",
      data: this.state
    }).done((msg) => {
      alert('Message sent');
    }).fail((error) => {
      alert('Message error');
    }).always(() => {
      this.resetForm();
    });

    event.preventDefault();
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <div className="row">
          <div className="six columns">
            <label htmlFor="textSMSInput">Text SMS</label>
            <input
              className="u-full-width"
              type="tel"
              placeholder="+12345678911" 
              id="textSMSInput"
              name="phone_number"
              value={this.state.phone_number} 
              onChange={this.handleChange} />
          </div>
          <div className="six columns">
            <label htmlFor="exampleRecipientInput">Reason htmlFor contacting</label>
            <select 
              className="u-full-width"
              id="exampleRecipientInput"
              name="choice_input"
              value={this.state.choice_input}
              onChange={this.handleChange}>
              <option value="Option 1">Questions</option>
              <option value="Option 2">Admiration</option>
              <option value="Option 3">Can I get your number?</option>
            </select>
          </div>
        </div>
        <label htmlFor="exampleMessage">Message</label>
        <textarea 
          className="u-full-width"
          placeholder="Hi Represent "
          id="exampleMessage"
          name="message"
          value={this.state.message}
          onChange={this.handleChange} />
        <label className="example-send-yourself-copy">
          <input type="checkbox" />
          <span className="label-body">Send a copy to yourself</span>
        </label>
        <input className="button-primary" type="submit" value="Submit" />
        <div className="fb-share-button" data-href="http://www.esquire.com/news-politics/a12775932/sackler-family-oxycontin/" data-layout="button" data-size="large" data-mobile-iframe="true"><a className="fb-xfbml-parse-ignore" target="_blank" rel="noopener noreferrer" href="https://www.facebook.com/sharer/sharer.php?u=http%3A%2F%2Fwww.esquire.com%2Fnews-politics%2Fa12775932%2Fsackler-family-oxycontin%2F&amp;src=sdkpreparse">Share</a></div>
      </form>
    );
  }
}

export default App;



